body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Poppins", sans-serif;
  background: #fafafa;
  overflow-x: hidden;
}

.imgFruit {
  width: 100px;
  height: 150px;
}

figure {
  text-align: center;
  float: left;
}

figcaption {
  text-align: center;
}
.col-md-4 {
  margin-bottom: 30px;
  position: relative;
}

.emptyDiv {
  opacity: 0.5;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}

.empty {
  opacity: 0.6;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  position: absolute;
  top: 60px;
  left: 15px;
}

.wrapper-inside {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: stretch;
          align-items: stretch;
}

#sideBar {
  background-color: #007bff;
  margin-right: -300px;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  border-top: 1px solid white;
  min-width: 300px;
  min-height: 100vh;
}

#sideBar.active {
  margin-right: 0px;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}

.container-fluid {
  margin-top: 15px;
}

.ordertable {
  margin: 0 auto;
  color: white;
  margin-top: 30px;
  width: 100%;
  padding-bottom: 20px;
  border-bottom: 1px solid white;
  margin-bottom: 30px;
}

.title {
  text-align: center;
  color: white;
}

.total {
  text-align: center;
  color: white;
  font-size: 2em;
}

.nav-link {
  font-size: 1.5em;
}

table tr:hover {
  background-color: #0767ce;
  cursor: pointer;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}

.buttonOrder {
  text-align: center;
}

.small-fotos {
  width: 50px;
  height: 50px;
}

.list-unstyled {
  border: 1px solid rgb(180, 178, 178);
}

h1 {
  text-align: center;
}

.orderTitle {
  background-color: rgb(202, 199, 199);
  padding: 10px;
  font-size: 15px;
  font-weight: bold;
}

.mr-2 {
  color: grey;
}

